@font-face {
    font-family: 'Peacechild';
    src:
        local('Peacechild'),
        url('peacechild-beta.ttf');
}

body {
    background-image: linear-gradient( rgb(255,130,130), rgb(253,188,180));
}

html, body {
    margin: 0;
    padding: 0;
}

header {
    font-size: 50px;
    font-family: Peacechild, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    outline: black;
}

.container {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    margin: auto;
    justify-content: center;
    height: 900px;
    width: 900px;
}

img {
    border-radius: 20px;
}